const handleMenuSelect = (index: string) => {
  let component;

  if (index.startsWith('http:') || index.startsWith('https:')) {
    // 如果是外部链接，直接在新标签页打开
    window.open(index, '_blank');
  } else {
    activeMenu.value = index;
